import { setContacts, setTasks, setCalendar, setRoutes } from '../actions'

const user = (state = {selected:0}, action) => {
  switch (action.type) {
    case 'SELECT_USER':
      console.log('SELECT_USER')
      setTimeout(()=>{
        // HACK: hard-coded for now
        const contacts = [
          { id:1, name:'Alden Harding', image:'./assets/ic_contact_01@2x.png' },
          { id:2, name:'Guo Tu', image:'./assets/ic_contact_02@2x.png' },
          { id:3, name:'Erika Johnson', image:'./assets/ic_contact_03@2x.png' },
        ];
        window.store.dispatch(setContacts(contacts));

        const tasks = [
          { id:1, title:"Send demo fore review", time:"11:00PM" },
          { id:2, title:"Call Brian", time:"8:30AM" },
        ];
        window.store.dispatch(setTasks(tasks));

        const calendar = [
          { id:1, title:"Morning staff meeting", loc:"Xevo Kirkland office",
            from:'10:00AM', to:'11:00AM' },
          { id:2, title:"XAssis scrum mtg", loc:"Starwars",
            from:'11:00AM', to:'12:00PM' },
        ];
        window.store.dispatch(setCalendar(calendar));

        const route0 = {stops:[
          { id:0, title:"Current location", loc:[-122.1897, 47.7097], image:"./assets/ic_pin_currentlocation@2x.png" },
          { id:1, title:"Xevo Kirkland Office", eta:"9:47 AM", loc:[-122.1736, 47.7126], image:"./assets/ic_pin_office@2x.png" }
        ]};
        window.store.dispatch(setRoutes([route0]));
      }, 500);
      return Object.assign({}, state, {selected:action.selected})

    default:
      return state
  }
}

export default user



