const layout = (state = {}, action) => {
  switch (action.type) {
    case 'SET_WINDOW_SIZE':
      state = {
        size:action.size,
        margin:8,
        rowHeight:20,
        radius:26, // for search and profile
        btnRadius:20, // for climate control
        bottomFrameHeight:action.size.height / 4,
        bottomBarHeight:action.size.height / 8,
      };
      Object.assign(state, {
        bottomFrameTop:state.size.height - state.bottomFrameHeight,
        bottomBarTop:state.size.height - state.bottomBarHeight,
      })
      return state;

    default:
      return state
  }
}

export default layout



