import React from 'react';
import PropTypes from 'prop-types';
import '../styles/Media.css';

const Media = ({layout}) => {
  return (
    <div style={{padding:layout.rowHeight, width:'100%', height:'100%',
    backgroundSize:'100%',
    backgroundImage:'url("./assets/img_album_nightvisions@2x.png")'}}>
      <h2>Radioactive 1</h2>
      <h3>Night Visions</h3>
      <h3>Image Dragons</h3>
    </div>
  );
}
Media.propTypes = {
  layout: PropTypes.object.isRequired,
}

export default Media;



